import React from "react";
import {
  Modal,
  Snippet,
  Button,
  Link,
  Tabs,
  Code,
  useClipboard,
  useToasts,
  Input,
  Select,
  useInput,
  Spacer,
} from "@geist-ui/react";
// @ts-ignore
import HTTPSnippet from "httpsnippet";
import { Copy } from "@geist-ui/react-icons";
import { useRecoilState, useRecoilValue } from "recoil";
import { requestState } from "../stores";
import { Base64 } from "../utils";
import { getAllCollection, createRequest } from "../db";

const thisWindow: any = window;

export function ModalShare(props: any) {
  const request = useRecoilValue(requestState);
  const { created_at, updated_at, status, ...rest } = request;
  const encoded = Base64.encode(JSON.stringify(rest));
  const text = ["https://pakpost.com/request?=", encoded].join("");
  return (
    <Modal {...props.bindings} open>
      <Modal.Title>Share</Modal.Title>
      <Modal.Content>
        <div style={{ marginBottom: 10 }}>
          <Snippet symbol=">" text={text} width="100%" />
        </div>
        <Link href="#" color style={{ fontSize: 12 }}>
          open link in new tab
        </Link>
      </Modal.Content>
    </Modal>
  );
}

function SnippetCode(props: any) {
  const generatedCode = props.snippet;
  const [, setToast] = useToasts();
  const { copy } = useClipboard();

  const handler = () => {
    copy(generatedCode);
    setToast({ text: "Code copied." });
  };

  return (
    <div style={{ minHeight: 400 }}>
      <Code block width="100%" style={{ maxHeight: "340px" }}>
        {generatedCode}
      </Code>
      <Button size="small" auto onClick={handler}>
        <Copy size={14} />{" "}
        <span style={{ paddingLeft: 10 }}>Copy to clipboard</span>
      </Button>
    </div>
  );
}

function transformObjectToArray(objectData: any) {
  const newArray: any = [];
  if (!objectData) return [];
  Object.keys(objectData).forEach(function (key: any) {
    newArray.push({ name: key, value: objectData[key] });
  });
  return newArray;
}

export function ModalCode(props: any) {
  const request = useRecoilValue(requestState);
  let payload: any = {
    method: request.method === "GRAPHQL" ? "POST" : request.method,
    url: request.url,
    httpVersion: "HTTP/1.1",
    headers: [
      {
        name: "content-type",
        value: "application/json",
      },
      ...transformObjectToArray(request.headers),
    ],
    postData: {
      mimeType: "application/json",
      text: JSON.stringify(request.data || {}),
    },
    cookies: [],
  };

  const snippet = new HTTPSnippet(payload);
  return (
    <Modal {...props.bindings} open width="50vw">
      <Modal.Title>Generate Code</Modal.Title>
      <Modal.Content>
        <Tabs initialValue="cURL">
          <Tabs.Item label="cURL" value="cURL">
            <SnippetCode
              snippet={snippet.convert("shell", "curl", {
                indent: "\t",
              })}
            />
          </Tabs.Item>
          <Tabs.Item label="Javascript" value="Javascript">
            <SnippetCode snippet={snippet.convert("javascript")} />
          </Tabs.Item>
          <Tabs.Item label="Node" value="Node">
            <SnippetCode snippet={snippet.convert("node", "unirest")} />
          </Tabs.Item>
          <Tabs.Item label="Go" value="Go">
            <SnippetCode snippet={snippet.convert("go")} />
          </Tabs.Item>
          <Tabs.Item label="Java" value="Java">
            <SnippetCode snippet={snippet.convert("java")} />
          </Tabs.Item>
        </Tabs>
      </Modal.Content>
    </Modal>
  );
}

export function ModalSaveRequest(props: any) {
  const input = useInput("");
  const [state, setstate] = React.useState([]);
  const [select, setSelect] = React.useState("");
  const [request, setRequest] = useRecoilState(requestState);

  React.useEffect(() => {
    getAllCollection().then((res: any) => setstate(res));
    if (request.collection_id) {
      setSelect(request.collection_id.toString());
    }
  }, [request.collection_id]);
  return (
    <Modal {...props.bindings} open width="30vw">
      <Modal.Title>Save request</Modal.Title>
      <Modal.Content>
        <Input
          label="label"
          width="100%"
          {...input.bindings}
          placeholder="Request label"
        />
        <Spacer />
        <Select
          onChange={(e) => setSelect(e.toString())}
          value={select}
          disabled={!!request.collection_id}
          width="100%"
          placeholder="select a collection"
        >
          {state.map((d: any, i) => (
            <Select.Option key={i} value={d.id.toString()}>
              {d.name}
            </Select.Option>
          ))}
        </Select>
      </Modal.Content>
      <Modal.Action passive onClick={() => props.setVisible(false)}>
        Close
      </Modal.Action>
      <Modal.Action
        onClick={() => {
          const { history_id, request_id, collection_id, ...rest } = request;
          createRequest({
            collection_id: Number(select),
            name: input.state,
            data: rest,
          }).then((request_id: number) => {
            setRequest((p) => ({ ...p, request_id, name: input.state }));
            thisWindow.getAllCollection();
            props.setVisible(false);
          });
        }}
      >
        Save
      </Modal.Action>
    </Modal>
  );
}
