import axios from "axios";

export const fetch = axios.create();

const axiosTimerFunc = (startTime: any) => {
  let now = Date.now();
  let seconds = Math.floor((now - startTime) / 1000);
  let milliseconds = Math.floor((now - startTime) % 1000);
  return `${seconds}.${milliseconds} s`;
};

fetch.interceptors.request.use((config) => {
  config.headers["pakpost-request-startTime"] = Date.now();
  return config;
});

fetch.interceptors.response.use(
  (response) => {
    const start = response.config.headers["pakpost-request-startTime"];
    response.headers["pakpost-request-duration"] = axiosTimerFunc(start);
    return response;
  },
  (error) => {
    const start = error.config.headers["pakpost-request-startTime"];
    error.config.headers["pakpost-request-duration"] = axiosTimerFunc(start);
    return Promise.reject({
      errors: error,
      dataErrors: error.response.data.errors,
    });
  }
);
