import React from "react";
import { Display, Text, Divider, Button, useModal } from "@geist-ui/react";
import ListRequest from "./ListRequest";
import { getAllCollection, getAllRequests } from "../db";
import CollectionMenu, { ModalCreateCollection } from "./CollectionMenu";

const thisWindow: any = window;
thisWindow.getAllCollection = function () {};

function getCollction(setstate: Function) {
  getAllCollection().then((data: any) => {
    getAllRequests().then((reData: any) => {
      setstate(
        data.map((d: any) => {
          const requests = reData.filter((r: any) => r.collection_id === d.id);
          return {
            ...d,
            total: requests.length,
            requests,
          };
        })
      );
    });
  });
}
export default function Collection(props: { filteredText: string }) {
  const [state, setstate] = React.useState([]);
  const modalAdd = useModal();

  React.useEffect(() => {
    getCollction(setstate);
    thisWindow.getAllCollection = function () {
      getCollction(setstate);
    };
  }, []);

  if (state.length < 1) {
    return (
      <div
        style={{
          overflow: "auto",
          height: "calc(100vh - 155px)",
          marginTop: -10,
        }}
      >
        <Display
          caption={
            <div>
              <Text size={12}>start create new collection</Text>
              <Button onClick={() => modalAdd.setVisible(true)} size="small">
                create collection
              </Button>
            </div>
          }
        >
          Collection is empty
        </Display>
        <ModalCreateCollection {...modalAdd} />
      </div>
    );
  }
  return (
    <div>
      <div className="flex spaceBetween" style={{ padding: 10 }}>
        <CollectionMenu />
      </div>
      <Divider y={1} style={{ margin: 0 }} />
      <div style={{ height: "calc(100vh - 210px)", overflow: "auto" }}>
        {state.map((d: any, i) => (
          <ListRequest key={i} {...d} />
        ))}
      </div>
    </div>
  );
}
