import React from "react";
import { useRecoilState } from "recoil";
import { requestState, uiState } from "../stores";
import DynamicTable from "./DynamicTable";

export default function MainRequestHeader() {
  const [request, setRequest] = useRecoilState(requestState);
  const [ui, setUi] = useRecoilState(uiState);
  return (
    <div>
      <DynamicTable
        data={request.headers}
        onChange={(headers: any) => setRequest((p) => ({ ...p, headers }))}
        height={"calc(30vh - 34px)"}
        useRaw
        activeTab={ui.requestHeaderTab}
        onChangeTab={(tab: string) =>
          setUi((p: any) => ({ ...p, requestHeaderTab: tab }))
        }
      />
    </div>
  );
}
