import { atom } from "recoil";
import { AxiosRequestConfig, AxiosResponse, Method } from "axios";

export interface RequestState extends Omit<AxiosRequestConfig, "method"> {
  history_id?: number | null;
  request_id?: number | null;
  collection_id?: number | null;
  created_at?: number;
  name?: string;
  updated_at?: number;
  "x-www-form-urlencoded"?: any;
  status: "LOADING" | "";
  method: Method | "GRAPHQL";
  schema?: any;
}

export interface ResponseState extends AxiosResponse {}

const DEFAULT_APP_STATE = {
  theme: "light",
  os: {
    platform: "",
    version: "",
  },
  app: {
    version: "",
    name: "",
  },
  darkMode: false,
  twoPane: false,
  noCacheHeader: true,
  followRedirects: true,
  trimRequestBody: false,
  sslVerification: false,
  useSytemProxy: true,
  useHttpRequest: true,
  proxyType: "https",
  server: "",
  port: 8080,
  auth: false,
  proxyPassword: "",
  proxyUsername: "",
};

const DEFAULT_UI_STATE = {
  sidebarTab: "history",
  sidebarSort: "",
  requestTab: "headers",
  requestBodyTab: "none",
  requestHeaderTab: "form-data",
  collectionTree: [0],
  showDocs: false,
};

const DEFAULT_ENVIRONTMENT_STATE = {
  selected: "",
  list: [],
};

export const DEFAULT_REQUEST_STATE: RequestState = {
  history_id: null,
  request_id: null,
  url: "",
  method: "GET",
  status: "",
  headers: [],
};

const DEFAULT_RESPONSE_STATE: ResponseState | any = null;

export const appState = atom({
  key: "appState",
  default: DEFAULT_APP_STATE,
});

export const requestState = atom({
  key: "requestState",
  default: DEFAULT_REQUEST_STATE,
});

export const responseState = atom({
  key: "responseState",
  default: DEFAULT_RESPONSE_STATE,
});

export const environtmentState = atom({
  key: "environtmentState",
  default: DEFAULT_ENVIRONTMENT_STATE,
});

export const uiState = atom({
  key: "uiState",
  default: DEFAULT_UI_STATE,
});

const stores = {
  appState: appState,
  requestState: requestState,
  responseState: responseState,
  uiState: uiState,
  environtmentState: environtmentState,
};

export default stores;
