import { encode, decode } from "js-base64";

export const Base64 = { encode, decode };

const thisWindow: any = window;

export function transformObjectToArray(objectData: any): any[] {
  const newArray: { name: string; value: any }[] = [];
  if (!objectData) return [];
  Object.keys(objectData).forEach(function (key) {
    newArray.push({ name: key, value: objectData[key] });
  });
  return newArray;
}

export function transformArrayToObject(arrayData: any[]): Object {
  let newObject: any = {};
  (arrayData || []).forEach((e: any) => {
    if (e.value && e.checked) newObject[e.name] = e.value;
  });
  return newObject;
}

export function formatBytes(bytes: number, decimals = 2) {
  if (bytes === 0) return "0 Bytes";

  const k = 1024;
  const dm = decimals < 0 ? 0 : decimals;
  const sizes = ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];

  const i = Math.floor(Math.log(bytes) / Math.log(k));

  return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + " " + sizes[i];
}

export const byteSize = (str: string) => formatBytes(new Blob([str]).size);

export const dbHelper = {
  getAllCollections: () => thisWindow.getAllCollection(),
  getAllHistory: () => thisWindow.getAllHistory(),
};
