import React from "react";
import { useTheme, Radio, Display, Text, Input } from "@geist-ui/react";
import Editor from "@monaco-editor/react";
import { useRecoilState } from "recoil";
import { requestState, uiState } from "../stores";
import DynamicTable from "./DynamicTable";

const thisWindow: any = window;

export default function MainRequestRestBody() {
  const { type, palette } = useTheme();
  const [request, setRequest] = useRecoilState(requestState);
  const [ui, setUi] = useRecoilState(uiState);
  const editorDataRef: any = React.useRef(null);

  React.useEffect(() => {
    if (ui.requestBodyTab === "binary") {
      thisWindow.tempFile = "";
    }
  }, [ui.requestBodyTab]);

  function editorDataDidMount(_: any, editor: any) {
    editorDataRef.current = editor;
  }

  function onMouseLeaveEditorData() {
    if (editorDataRef.current && editorDataRef.current.getValue()) {
      setRequest((p) => ({
        ...p,
        data: JSON.parse(editorDataRef.current.getValue()),
      }));
    }
  }

  return (
    <div>
      <div
        style={{
          padding: "10px",
          borderBottom: `1px solid ${palette.accents_2}`,
        }}
      >
        <Radio.Group
          value={ui.requestBodyTab}
          onChange={(e) => setUi((p: any) => ({ ...p, requestBodyTab: e }))}
          useRow
          size="mini"
        >
          <Radio value="none">None</Radio>
          <Radio value="raw">raw</Radio>
          <Radio value="x-www-form-urlencoded">x-www-form-urlencoded</Radio>
          <Radio value="binary">binary</Radio>
        </Radio.Group>
      </div>
      {ui.requestBodyTab === "none" && (
        <Display
          caption={<Text>This request does not have a body</Text>}
        ></Display>
      )}
      {ui.requestBodyTab === "raw" && (
        <div onMouseLeave={onMouseLeaveEditorData}>
          <Editor
            height="calc(30vh - 42px)"
            editorDidMount={editorDataDidMount}
            theme={type}
            value={JSON.stringify(request.data || {}, null, 2)}
            options={{
              minimap: { enabled: false },
              renderLineHighlight: false,
            }}
            language="json"
          />
        </div>
      )}
      {ui.requestBodyTab === "x-www-form-urlencoded" && (
        <DynamicTable
          height="calc(30vh - 42px)"
          activeTab="form-data"
          onChange={(data: any) => {
            setRequest((p) => ({ ...p, "x-www-form-urlencoded": data }));
          }}
          data={request?.["x-www-form-urlencoded"] || []}
        />
      )}
      {ui.requestBodyTab === "binary" && (
        <div style={{ padding: 10 }}>
          <Input
            size="small"
            onChange={(e) => {
              thisWindow.tempFile = e.target.files ? e.target.files[0] : "";
            }}
            type="file"
          />
        </div>
      )}
    </div>
  );
}
