import React from "react";
import {
  Button,
  Text,
  Select,
  Display,
  Spacer,
  Popover,
  Divider,
} from "@geist-ui/react";
import { getAllHistory, deleteHistory } from "../db";
import _orderBy from "lodash/orderBy";
import ListItem from "./ListItem";
import { useRecoilValue, useResetRecoilState } from "recoil";
import { requestState } from "../stores";

function getListHistory(setList: Function) {
  getAllHistory().then((data: any) => {
    setList(data);
  });
}

let thisWindow: any = window;
thisWindow.getListHistory = function () {};

export default function History(props: { filteredText: string }) {
  const [list, setList] = React.useState([]);
  const [visible, setVisible] = React.useState(false);
  const [sort, setSort] = React.useState("updated_at");
  const resetRequest = useResetRecoilState(requestState);
  const request = useRecoilValue(requestState);

  React.useEffect(() => {
    thisWindow.getListHistory = function () {
      getListHistory(setList);
    };
    getListHistory(setList);
  }, []);

  const filteredList = list.filter((d: any) => {
    return d.url.toLowerCase().includes(props.filteredText.toLowerCase());
  });

  return (
    <>
      <div
        className="flex spaceBetween"
        style={{ padding: "10px 10px 0px 10px" }}
      >
        <Popover
          placement="bottomStart"
          content={
            <div style={{ padding: "10px 20px" }}>
              <Text small type="secondary">
                Are you sure you want to clear all your history?
              </Text>
              <Spacer y={0.5} />
              <Button
                size="mini"
                onClick={() => {
                  deleteHistory().then(() => {
                    getListHistory(setList);
                    if (request.history_id) resetRequest();
                    setVisible(false);
                  });
                }}
              >
                Yes
              </Button>
              <Spacer x={1} inline />
              <Button
                type="secondary-light"
                size="mini"
                onClick={() => setVisible(false)}
              >
                No
              </Button>
            </div>
          }
          visible={visible}
          onVisibleChange={setVisible}
        >
          <Button onClick={() => setVisible(true)} size="mini">
            clear all
          </Button>
        </Popover>
        <Select
          size="mini"
          initialValue={sort.toString()}
          placeholder="Sort by"
          onChange={(e) => setSort(e.toString())}
          width="100px"
        >
          <Select.Option value="url">Label</Select.Option>
          <Select.Option value="updated_at">Time</Select.Option>
          <Select.Option value="method">Method</Select.Option>
        </Select>
      </div>
      <Divider y={1} />
      <div
        style={{
          overflow: "auto",
          height: "calc(100vh - 210px)",
          marginTop: -10,
        }}
      >
        {filteredList.length < 1 && !props.filteredText && (
          <Display
            caption={
              <Text size={12}>any request you send will aprear here</Text>
            }
          >
            History is empty
          </Display>
        )}
        {filteredList.length < 1 && props.filteredText && (
          <Display
            caption={<Text size={12}>Please try another keyword</Text>}
          />
        )}
        {[..._orderBy(filteredList, [sort], ["desc"])].map((d: any, i) => (
          <ListItem key={i} {...d} />
        ))}
      </div>
    </>
  );
}
