import Dexie from "dexie";

const db: any = new Dexie("pakpost");

export const putHistory = (args: any) => {
  let payload: any = Object.assign({}, args);
  const now = Date.now();
  if (!payload.created_at) {
    payload.created_at = now;
  }
  payload.updated_at = now;
  const { id, ...rest } = payload;
  if (id) {
    return db.history.update(id, rest);
  }
  return db.history.add(rest);
};

export const getAllHistory = () => {
  return db.history.toArray();
};

export const getHistoryById = (id: string) => {
  return db.history.get(id);
};

export const deleteHistory = () => {
  return db.history.clear();
};
export const deleteHistoryById = (id: number) => {
  return db.history.delete(id);
};

export const getAllCollection = () => {
  return db.collections.toArray();
};

export const createCollection = (name: string) => {
  return db.collections.add({
    name,
    created_at: Date.now(),
    updated_at: Date.now(),
  });
};

export const updateCollection = (id: number, payload: any) => {
  return db.collections.add(id, {
    ...payload,
    updated_at: Date.now(),
  });
};

export const deleteCollectionById = (id: number) => {
  return db.collections.delete(id);
};

export const deleteCollection = () => {
  return db.collections.clear();
};

export const getAllRequests = () => {
  return db.requests.toArray();
};

export const createRequest = (payload: any) => {
  return db.requests.add({
    ...payload,
    created_at: Date.now(),
    updated_at: Date.now(),
  });
};

export const updateRequest = (id: number, payload: any) => {
  return db.requests.update(id, {
    ...payload,
    updated_at: Date.now(),
  });
};

export const deleteRequest = () => {
  return db.requests.clear();
};

export const deleteRequestById = (id: number) => {
  return db.requests.delete(id);
};

export default db;
