import React from "react";
import {
  useTheme,
  Tabs,
  Button,
  Display,
  Text,
  useClipboard,
  useToasts,
} from "@geist-ui/react";
import Editor from "@monaco-editor/react";
import { useRecoilValue } from "recoil";
import _isEmpty from "lodash/isEmpty";
import { requestState, responseState } from "../stores";
import { byteSize } from "../utils";
import { ReactComponent as DeliveryIcon } from "../svgs/delivery.svg";
import { ReactComponent as ShippingIcon } from "../svgs/shipping.svg";

function ResponseError(props: any) {
  const { type } = useTheme();
  const { dataErrors, errors } = props.response;
  const status = "ERR";
  const duration = errors.config.headers["pakpost-request-duration"];
  let body = { message: errors.message };
  if (dataErrors) {
    body = dataErrors;
  }
  const stringData = JSON.stringify(body);
  return (
    <>
      <div style={{ fontSize: 12, position: "absolute", right: 15, top: 7 }}>
        <div className="flex padding-row">
          <div>
            <Text type="secondary" small>
              Status :
            </Text>
            <Text small>{status}</Text>
          </div>
          <div>
            <Text type="secondary" small>
              Time :
            </Text>
            <Text small> {duration}</Text>
          </div>
          <div>
            <Text type="secondary" small>
              Size :
            </Text>
            <Text small> {byteSize(stringData)}</Text>
          </div>
          <div>
            <Button onClick={() => props.onCopy(stringData)} size="mini">
              Copy Response
            </Button>
          </div>
        </div>
      </div>
      <Tabs initialValue="body">
        <Tabs.Item value="body" label="body">
          <Editor
            height="calc(70vh - 235px)"
            value={JSON.stringify(body, null, 2)}
            language="json"
            theme={type}
            options={{
              minimap: { enabled: false },
              renderLineHighlight: false,
            }}
          />
        </Tabs.Item>
        <Tabs.Item value="Headers" label="Headers">
          <Editor
            height="calc(70vh - 235px)"
            value={JSON.stringify(errors.config.headers, null, 2)}
            language="json"
            theme={type}
            options={{
              minimap: { enabled: false },
              renderLineHighlight: false,
            }}
          />
        </Tabs.Item>
      </Tabs>
    </>
  );
}

function ResponseSuccess(props: any) {
  const { response } = props;
  const { type } = useTheme();
  const stringData = JSON.stringify(response?.data || {});
  const status = response.status + " " + response.statusText;
  const duration = response.headers["pakpost-request-duration"];
  const headers = { ...response.headers };
  delete headers["pakpost-request-duration"];
  return (
    <>
      <div style={{ fontSize: 12, position: "absolute", right: 15, top: 7 }}>
        <div className="flex padding-row">
          <div>
            <Text type="secondary" small>
              Status :
            </Text>
            <Text small>{status}</Text>
          </div>
          <div>
            <Text type="secondary" small>
              Time :
            </Text>
            <Text small> {duration}</Text>
          </div>
          <div>
            <Text type="secondary" small>
              Size :
            </Text>
            <Text small> {byteSize(stringData)}</Text>
          </div>
          <div>
            <Button onClick={() => props.onCopy(stringData)} size="mini">
              Copy Response
            </Button>
          </div>
        </div>
      </div>
      <Tabs initialValue="body">
        <Tabs.Item value="body" label="body">
          <Editor
            height="calc(70vh - 235px)"
            value={JSON.stringify(response.data, null, 2)}
            language="json"
            theme={type}
            options={{
              minimap: { enabled: false },
              renderLineHighlight: false,
            }}
          />
        </Tabs.Item>
        <Tabs.Item value="Headers" label="Headers">
          <Editor
            height="calc(70vh - 235px)"
            value={JSON.stringify(headers, null, 2)}
            language="json"
            theme={type}
            options={{
              minimap: { enabled: false },
              renderLineHighlight: false,
            }}
          />
        </Tabs.Item>
      </Tabs>
    </>
  );
}

export default () => {
  const { palette, type } = useTheme();
  const response = useRecoilValue(responseState);
  const { copy } = useClipboard();
  const [, setToast] = useToasts();
  const request = useRecoilValue(requestState);
  const color = type === "light" ? "#000" : "#fff";

  function onCopy(stringData: string) {
    copy(stringData);
    setToast({ text: "Response copied." });
  }

  if (request.status === "LOADING") {
    return (
      <div
        className="flex center relative"
        style={{ borderTop: `1px solid ${palette.accents_2}` }}
      >
        <div style={{ width: 300 }}>
          <Display
            caption={<Text>Please wait, your package is on delivery</Text>}
          >
            <ShippingIcon fill={color} width={100} height={100} />
          </Display>
        </div>
      </div>
    );
  }

  if (_isEmpty(response)) {
    return (
      <div
        className="flex center relative"
        style={{ borderTop: `1px solid ${palette.accents_2}` }}
      >
        <div style={{ width: 300 }}>
          <Display
            caption={
              <Text>
                there is no package to deliver. Hit Send to get a response
              </Text>
            }
          >
            <DeliveryIcon fill={color} width={100} height={100} />
          </Display>
        </div>
      </div>
    );
  }

  return (
    <div
      className="mainresponse relative"
      style={{
        flexGrow: 1,
        paddingTop: 5,
        borderTop: `1px solid ${palette.accents_2}`,
      }}
    >
      {response.errors ? (
        <ResponseError response={response} onCopy={onCopy} />
      ) : (
        <ResponseSuccess response={response} onCopy={onCopy} />
      )}
    </div>
  );
};
