import React from "react";
import { useTheme, Tabs, Divider, Button } from "@geist-ui/react";
import Editor from "@monaco-editor/react";
import { useRecoilState } from "recoil";
import { requestState, uiState } from "../stores";
import MainRequestHeader from "./MainRequestHeader";
import DocsGraphQl from "./DocsGraphQl";

export default function GraphQlRequest() {
  const [request, setRequest] = useRecoilState(requestState);
  const { palette, type } = useTheme();
  const [ui, setUi] = useRecoilState(uiState);
  const editorQueryRef: any = React.useRef(null);
  const editorVariablesRef: any = React.useRef(null);

  function editorQueryDidMount(_: any, editor: any) {
    editorQueryRef.current = editor;
  }

  function onMouseLeaveEditorQuery() {
    if (editorQueryRef.current && editorQueryRef.current.getValue()) {
      setRequest((p) => ({
        ...p,
        data: {
          ...p.data,
          query: editorQueryRef.current.getValue(),
        },
      }));
    }
  }

  function editorVariablesDidMount(_: any, editor: any) {
    editorVariablesRef.current = editor;
  }

  function onMouseLeaveEditorVariables() {
    if (editorVariablesRef.current && editorVariablesRef.current.getValue()) {
      setRequest((p) => ({
        ...p,
        data: {
          ...p.data,
          variables: JSON.parse(editorVariablesRef.current.getValue()),
        },
      }));
    }
  }

  return (
    <div
      style={{
        height: "calc(30vh + 45px)",
        position: "relative",
      }}
    >
      <DocsGraphQl />
      <Tabs
        initialValue={ui.requestTab}
        onChange={(e) => setUi((p) => ({ ...p, requestTab: e }))}
      >
        <Tabs.Item label="Headers" value="Headers">
          <MainRequestHeader />
        </Tabs.Item>
        <Tabs.Item label="Body" value="Body">
          <div className="flex">
            <div onMouseLeave={onMouseLeaveEditorQuery}>
              <div
                style={{
                  paddingLeft: 15,
                  marginTop: 5,
                  fontSize: 12,
                  fontWeight: 700,
                  marginBottom: 5,
                }}
              >
                Query
              </div>
              <Editor
                height="calc(30vh - 15px)"
                width="calc(50vw - 130px)"
                editorDidMount={editorQueryDidMount}
                theme={type}
                value={request?.data?.query || "{}"}
                language="graphql"
                options={{
                  minimap: { enabled: false },
                  renderLineHighlight: false,
                }}
              />
            </div>
            <Divider y={0} />
            <div
              style={{
                borderLeft: `1px solid ${palette.accents_2}`,
              }}
              onMouseLeave={onMouseLeaveEditorVariables}
            >
              <div
                style={{
                  paddingLeft: 15,
                  marginTop: 5,
                  fontSize: 12,
                  fontWeight: 700,
                  marginBottom: 5,
                }}
              >
                Variables
              </div>
              <Editor
                height="calc(30vh - 15px)"
                width="calc(50vw - 130px)"
                editorDidMount={editorVariablesDidMount}
                theme={type}
                value={JSON.stringify(request?.data?.variables || {}, null, 2)}
                language="json"
                options={{
                  minimap: { enabled: false },
                  renderLineHighlight: false,
                }}
              />
            </div>
          </div>
        </Tabs.Item>
      </Tabs>
    </div>
  );
}
