import React from "react";
import {
  useTheme,
  ButtonGroup,
  Button,
  useModal,
  Select,
  Popover,
} from "@geist-ui/react";
import styles from "./Header.module.css";
import { Settings, Eye, Layers } from "@geist-ui/react-icons";
import { useRecoilState } from "recoil";
import { environtmentState } from "../stores";
import ModalEnvirontment from "./ModalEnvirontment";
import ModalImport from "./ModalImport";
import SettingsPage from "./Settings";
import useSendRequest from "../hooks/useSendRequest";
import ViewCurrentEnv from "./ViewCurrentEnv";

export default () => {
  const { palette } = useTheme();
  const { setVisible, bindings } = useModal();
  const modalImport = useModal();
  const [showSetting, setShowSetting] = React.useState(false);
  const { newRequest } = useSendRequest();
  const [env, setEnv] = useRecoilState(environtmentState);

  const onChange = (e: any) =>
    e === "ADD" ? setVisible(true) : setEnv((p) => ({ ...p, selected: e }));

  return (
    <div
      className={styles.header}
      style={{ borderBottom: `1px solid ${palette.accents_2}` }}
    >
      <div>
        <ButtonGroup size="mini">
          <Button onClick={() => newRequest()}>New Request</Button>
          <Button onClick={() => modalImport.setVisible(true)}>Import</Button>
        </ButtonGroup>
      </div>
      <div className="padding-row flex" style={{ marginRight: 10 }}>
        <ButtonGroup size="mini">
          <Select
            size="mini"
            placeholder="Environtments"
            value={env.selected || ""}
            onChange={onChange}
          >
            <Select.Option value="">No Environtment</Select.Option>
            {env.list.map((d: any, i) => (
              <Select.Option key={i} value={d.id}>
                {d.name}
              </Select.Option>
            ))}
          </Select>
          {!env.selected ? (
            <Button disabled>
              <Eye size={16} />
            </Button>
          ) : (
            <Popover placement="bottomEnd" content={<ViewCurrentEnv />}>
              <Button>
                <Eye size={16} />
              </Button>
            </Popover>
          )}

          <Button onClick={() => setVisible(true)}>
            <Layers size={16} />
          </Button>
        </ButtonGroup>
        <button
          onClick={() => setShowSetting(true)}
          style={{ width: 20, textAlign: "center" }}
        >
          <Settings size={16} />
        </button>
      </div>
      <ModalEnvirontment bindings={bindings} setVisible={setVisible} />
      <ModalImport
        bindings={modalImport.bindings}
        setVisible={modalImport.setVisible}
      />
      {showSetting && <SettingsPage setShowSetting={setShowSetting} />}
    </div>
  );
};
