import React from "react";
import db from "../db";

export default function useDbValue(
  table: "collections" | "history" | "requests",
  id?: number | null
) {
  const [state, setstate] = React.useState(null);
  React.useEffect(() => {
    if (id && table) {
      db[table].get(id).then((result: any) => {
        setstate(result);
      });
    } else {
      setstate(null);
    }
  }, [id, table]);
  return state;
}
