import React from "react";
import { Modal, Input, useInput, useToasts } from "@geist-ui/react";
import { Base64 } from "../utils";
import { useRecoilState, useResetRecoilState } from "recoil";
import { requestState, responseState } from "../stores";

export default function ModalImport(props: any) {
  const input = useInput("");
  const [, setToast] = useToasts();
  const [, setRequest] = useRecoilState(requestState);
  const resetRequest = useResetRecoilState(responseState);

  function onClick() {
    const string = input.state.split("request?=");
    if (!string[0] || !string[0].includes("pakpost.com")) {
      setToast({
        text: "Pak post url is invalid",
        type: "error",
      });
      return;
    }
    setRequest(JSON.parse(Base64.decode(string[1])));
    resetRequest();
    props.setVisible(false);
  }

  return (
    <Modal width="40vw" {...props.bindings}>
      <Modal.Title>Import from URL</Modal.Title>
      <Modal.Content>
        <Input
          width="100%"
          {...input.bindings}
          placeholder="eg: https://pakpost.com/request=abcdefg"
        />
      </Modal.Content>
      <Modal.Action passive onClick={() => props.setVisible(false)}>
        Close
      </Modal.Action>
      <Modal.Action onClick={onClick}>Open</Modal.Action>
    </Modal>
  );
}
