import React from "react";
import {
  Modal,
  Input,
  Button,
  Text,
  useToasts,
  useTheme,
  useInput,
  Display,
} from "@geist-ui/react";
import { Minus, Plus } from "@geist-ui/react-icons";
import { useRecoilState } from "recoil";
import { environtmentState } from "../stores";
import DynamicTable from "./DynamicTable";

function List(props: any) {
  const { palette } = useTheme();
  const [, setEnv] = useRecoilState(environtmentState);
  function onclick() {
    setEnv((p) => ({
      ...p,
      list: p.list.filter((d: any) => d.id !== props.id),
    }));
  }
  return (
    <div
      className="flex spaceBetween"
      style={{
        marginBottom: 5,
        borderTop: `1px solid ${palette.accents_2}`,
        paddingTop: 5,
      }}
    >
      <button onClick={props.onClick}>
        <Text small size={12} type="secondary">
          {props.name}
        </Text>
      </button>
      <Button
        onClick={onclick}
        size="mini"
        style={{ minWidth: 0, padding: 10 }}
      >
        <Minus size={12} />
      </Button>
    </div>
  );
}

const INITIAL_STATE = {
  id: "",
  name: "",
  variables: [],
};

export default function ModalEnv(props: any) {
  const { palette } = useTheme();
  const inputNew = useInput("");
  const [env, setEnv] = useRecoilState(environtmentState);
  const [selected, setSelected] = React.useState(
    env.list.length > 0 ? env.list[0] : INITIAL_STATE
  );
  const [, setToast] = useToasts();

  function onAddNew() {
    setEnv((p: any) => ({
      ...p,
      list: [
        ...p.list,
        {
          name: inputNew.state,
          id: Date.now().toString(),
          variables: [],
        },
      ],
    }));
    inputNew.reset();
  }

  return (
    <Modal width="70vw" {...props.bindings}>
      <Modal.Content style={{ paddingTop: 0 }}>
        <div className="flex">
          <div
            style={{
              borderRight: `1px solid ${palette.accents_2}`,
              paddingRight: 15,
              marginRight: 15,
              width: 400,
            }}
          >
            <div
              className="flex spaceBetween"
              style={{
                paddingBottom: 10,
                borderBottom: `1px solid ${palette.accents_2}`,
              }}
            >
              <Input
                label="ADD"
                size="mini"
                {...inputNew.bindings}
                placeholder="new env"
              />
              <Button
                size="mini"
                style={{ minWidth: 25, width: 25, marginLeft: 10 }}
                type="secondary-light"
                disabled={!inputNew.state}
                onClick={onAddNew}
              >
                <Plus size={15} />
              </Button>
            </div>
            <div style={{ overflow: "auto", height: "calc(100% - 40px)" }}>
              {env.list.length < 1 && (
                <Display caption="create new environtment" />
              )}
              {env.list.map((d: any, i) => (
                <List {...d} key={i} onClick={() => setSelected(d)} />
              ))}
            </div>
          </div>
          <div style={{ flexGrow: 1, width: "100%" }}>
            {!selected.id ? (
              <div style={{ height: "calc(20vh + 105px)" }}>
                <Display
                  caption="An environment is a set of variables that allow you to
                    switch the context of your requests. Environments can be
                    shared between multiple workspaces"
                />
              </div>
            ) : (
              <>
                <div
                  className="flex spaceBetween"
                  style={{
                    paddingBottom: 10,
                    borderBottom: `1px solid ${palette.accents_2}`,
                  }}
                >
                  <Input
                    label="name"
                    value={selected.name}
                    onChange={(e) =>
                      setSelected({ ...selected, name: e.target.name })
                    }
                    size="mini"
                  />
                  <Button
                    size="mini"
                    onClick={() => {
                      setEnv((p: any) => ({
                        ...p,
                        list: p.list.map((d: any) =>
                          d.id === selected.id ? selected : d
                        ),
                      }));
                      setToast({
                        text: "environtment " + selected.name + " saved.",
                      });
                    }}
                    type="secondary-light"
                  >
                    Save
                  </Button>
                </div>
                <DynamicTable
                  data={selected.variables}
                  height="30vh"
                  hideChecked
                  onChange={(data: any) =>
                    setSelected({ ...selected, variables: data })
                  }
                />
              </>
            )}
          </div>
        </div>
      </Modal.Content>
    </Modal>
  );
}
