import React from "react";
import styles from "./Settings.module.css";
import {
  Tabs,
  Text,
  Grid,
  Button,
  Toggle,
  Checkbox,
  Spacer,
  Link,
  Radio,
  Input,
  useTheme,
  Divider,
} from "@geist-ui/react";
// @ts-ignore
import exportFromJSON from "export-from-json";

import { X } from "@geist-ui/react-icons";
import { useRecoilState, useRecoilValue } from "recoil";
import { appState } from "../stores";
import { getAllCollection, getAllRequests } from "../db";

const REQUEST = [
  {
    dataIndex: "trimRequestBody",
    label: "Trim keys and value in request body",
  },
  {
    dataIndex: "sslVerification",
    label: "SSL certificate verification",
  },
  {
    dataIndex: "useHttpRequest",
    label: "use HttpRequest instead of XMLHttpRequest",
  },
];

const HEADERS = [
  {
    dataIndex: "noCacheHeader",
    label: "Send no-cache header",
  },
  {
    dataIndex: "followRedirects",
    label: "Automatically follow redirects",
  },
];

const UI = [
  {
    dataIndex: "twoPane",
    label: "Two-pane view",
  },
  {
    dataIndex: "darkMode",
    label: "Dark Mode",
  },
];

function List(props: any) {
  const [app, setApp] = useRecoilState<any>(appState);
  return (
    <>
      <div className="flex spaceBetween">
        <Text small p style={{ margin: 0 }} size={12} type="secondary">
          {props.label}
        </Text>
        <Toggle
          checked={app[props.dataIndex]}
          onChange={(e) => {
            setApp((p: any) => ({ ...p, [props.dataIndex]: e.target.checked }));
          }}
        />
      </div>
      <Divider y={1} />
    </>
  );
}

export default function Settings(props: {
  setShowSetting: (args: boolean) => void;
}) {
  const [app, setApp] = useRecoilState(appState);
  const { palette } = useTheme();

  function onChange(key: string, value: any) {
    setApp((p) => ({ ...p, [key]: value }));
  }
  console.log(app);
  return (
    <div className={styles.setting} style={{ background: palette.background }}>
      <button
        onClick={() => props.setShowSetting(false)}
        style={{ position: "absolute", right: 20 }}
      >
        <X />
      </button>
      <Tabs initialValue="General">
        <Tabs.Item label="General" value="General">
          <Grid.Container gap={5} style={{ padding: 20 }} justify="center">
            <Grid xs={8}>
              <Text h6>REQUEST</Text>
              {REQUEST.map((d: any, i: number) => (
                <List key={i} {...d} />
              ))}
            </Grid>
            <Grid xs={8}>
              <Text h6>HEADERS</Text>
              {HEADERS.map((d: any, i: number) => (
                <List key={i} {...d} />
              ))}
            </Grid>
            <Grid xs={8}>
              <Text h6>USER INTERFACE</Text>
              {UI.map((d: any, i: number) => (
                <List key={i} {...d} />
              ))}
            </Grid>
          </Grid.Container>
        </Tabs.Item>
        <Tabs.Item label="Proxy" value="Proxy">
          <div style={{ padding: 20 }}>
            <Text h6>Proxy configurations for sending requests</Text>
            <Text small p style={{ margin: 0 }} type="secondary">
              Specify a proxy setting to act as an intermediary for requests
              sent through the Builder in PakPost.
            </Text>
            <Spacer y={1} />
            <Radio.Group
              size="small"
              value={app.useSytemProxy ? "1" : "0"}
              onChange={(e) => onChange("useSytemProxy", !!Number(e))}
            >
              <Radio value="1">Use the system proxy</Radio>
              <Radio value="0">Add a custom proxy configuration</Radio>
            </Radio.Group>
            {!app.useSytemProxy && (
              <div style={{ width: 402, padding: 20 }}>
                <Radio.Group
                  value={app.proxyType.toString()}
                  onChange={(e) => onChange("proxyType", Number(e))}
                  useRow
                  size="small"
                >
                  <Radio value="http">HTTP</Radio>
                  <Radio value="https">HTTPS</Radio>
                </Radio.Group>
                <Spacer y={0.5} />
                <div>
                  <Input
                    width="242px"
                    size="small"
                    label="Server"
                    placeholder="127.0.0.1"
                    initialValue={app.server}
                    onChange={(e) => onChange("server", e)}
                  />
                  <Spacer x={0.5} inline />
                  <Input
                    size="small"
                    placeholder="8080"
                    type="number"
                    initialValue={app.port.toString()}
                    onChange={(e) => onChange("port", Number(e))}
                  />
                </div>
                <Spacer y={0.5} />
                <Input
                  size="small"
                  width="100%"
                  label="username"
                  placeholder="username"
                  initialValue={app.proxyUsername}
                  onChange={(e) =>
                    onChange("proxyUsername", Number(e.target.value))
                  }
                />
                <Spacer y={0.5} />
                <Input
                  size="small"
                  width="100%"
                  label="password"
                  placeholder="password"
                  type="password"
                  initialValue={app.proxyPassword}
                  onChange={(e) =>
                    onChange("proxyPassword", Number(e.target.value))
                  }
                />
              </div>
            )}
          </div>
        </Tabs.Item>
        <Tabs.Item label="Data" value="Data">
          <div style={{ padding: 20, width: "80%" }}>
            <Text h6>Export data</Text>
            <Text p small>
              Export all your collections, environments, globals and header
              presets to a single dump file. Note: Exporting a data dump will
              save your globals from different workspaces as individual
              environments.
            </Text>
            <Button
              onClick={() => {
                getAllCollection().then((allCollection: any) => {
                  getAllRequests().then((allRequest: any) => {
                    exportFromJSON({
                      data: {
                        collections: allCollection,
                        requests: allRequest,
                      },
                      fileName: "pakpost_exported_data",
                      exportType: "json",
                    });
                  });
                });
              }}
              size="medium"
            >
              Downlaod
            </Button>
          </div>
        </Tabs.Item>
        <Tabs.Item label="Update" value="Update">
          <div style={{ padding: 20, width: "80%" }}>
            <Text h6>You are up to date!</Text>
            <Text p small>
              {app.app.name} v{app.app.version} is the latest version.
            </Text>
            <Button size="medium">Check for Updates</Button>
          </div>
        </Tabs.Item>
        <Tabs.Item label="About" value="About">
          <div style={{ padding: 20, width: "80%" }}>
            <Text h1>Pakpost</Text>
            <Text p small>
              {app.app.name} for {app.os.platform === "darwin" ? "Mac OS" : ""}{" "}
              <br />
              Version {app.app.version} <br />
              {app.os.platform === "darwin" ? "OS X" : ""} {app.os.version}
            </Text>
            <Link href="#" color>
              Website
            </Link>
            <br />
            <Link href="#" color>
              Report an issue
            </Link>
          </div>
        </Tabs.Item>
      </Tabs>
    </div>
  );
}
