import { useRecoilState } from "recoil";
import { useToasts } from "@geist-ui/react";
import { requestState } from "../stores";
import { updateRequest } from "../db";

export default function useSaveRequest() {
  const [request] = useRecoilState(requestState);
  const [, setToast] = useToasts();

  const saveRequest = (modalSave: any) => {
    if (request.request_id) {
      const { request_id, history_id, ...rest } = request;
      updateRequest(request_id, { data: rest }).then(() => {
        setToast({ text: "Request saved." });
      });
      return;
    }
    modalSave.setVisible(true);
  };

  return {
    saveRequest,
  };
}
