import React from "react";
import { Popover, Modal, useModal } from "@geist-ui/react";
import { MoreVertical } from "@geist-ui/react-icons";
import db from "../db";
import useSendRequest from "../hooks/useSendRequest";

export default function ListMore(props: { db: string; id: number }) {
  const { visible, setVisible, bindings } = useModal();
  const [show, setShow] = React.useState(false);
  const { newRequest } = useSendRequest();
  const isCollection = props.db === "collections";

  function onDelete() {
    const thisWindow: any = window;
    db[props.db].delete(props.id).then(() => {
      if (isCollection) {
        db.requests.toArray().then((data: any) => {
          if (data.length < 1) {
            thisWindow.getAllCollection();
            setVisible(false);
            return;
          }
          const filtered = data.filter(
            (d: any) => d.collection_id === props.id
          );
          if (filtered.length > 0) {
            db.requests
              .bulkDelete([...filtered.map((d: any) => d.id)])
              .then(() => {
                thisWindow.getAllCollection();
              })
              .catch(() => {})
              .finally(() => {
                setVisible(false);
              });
          } else {
            thisWindow.getAllCollection();
            setVisible(false);
          }
        });
        return;
      }

      thisWindow.getAllCollection();
      setVisible(false);
    });
  }

  return (
    <>
      <Popover
        placement="right"
        visible={show}
        content={
          <>
            {isCollection && (
              <Popover.Item title>
                <button
                  onClick={() => {
                    newRequest(props.id);
                    setShow(false);
                  }}
                >
                  New Request
                </button>
              </Popover.Item>
            )}
            <Popover.Item title>
              <button>Edit</button>
            </Popover.Item>
            <Popover.Item>
              <button
                onClick={() => {
                  setVisible(true);
                  setShow(false);
                }}
              >
                Delete
              </button>
            </Popover.Item>
          </>
        }
      >
        <button onClick={() => setShow(true)}>
          <MoreVertical size={14} color="grey" />
        </button>
      </Popover>
      {visible && (
        <Modal {...bindings}>
          <Modal.Title>Are you sure want to delete ?</Modal.Title>
          {isCollection && (
            <Modal.Subtitle style={{ textTransform: "lowercase" }}>
              this proses will delete all requests related to it
            </Modal.Subtitle>
          )}
          <Modal.Action passive onClick={() => setVisible(false)}>
            Cancel
          </Modal.Action>
          <Modal.Action type="error" onClick={onDelete}>
            Delete
          </Modal.Action>
        </Modal>
      )}
    </>
  );
}
