import React from "react";
import { useTheme, Tabs, Input, useInput } from "@geist-ui/react";
import Collection from "./Collection";
import History from "./History";
import { useRecoilState } from "recoil";
import { uiState } from "../stores";

export default function () {
  const { palette } = useTheme();
  const [ui, setUi] = useRecoilState(uiState);
  const input = useInput("");

  return (
    <div
      className="sidebar"
      style={{ width: 260, borderRight: `1px solid ${palette.accents_2}` }}
    >
      <div style={{ padding: 10 }}>
        <Input
          {...input.bindings}
          label="search"
          placeholder="type to search"
          size="mini"
          width="100%"
        />
      </div>
      <div>
        <Tabs
          initialValue={ui.sidebarTab}
          onChange={(e) => setUi((p) => ({ ...p, sidebarTab: e }))}
        >
          <Tabs.Item label="history" value="history">
            <History filteredText={input.state} />
          </Tabs.Item>
          <Tabs.Item label="collections" value="collections">
            <Collection filteredText={input.state} />
          </Tabs.Item>
        </Tabs>
      </div>
    </div>
  );
}
