import React from "react";
import { Table, Text, Code } from "@geist-ui/react";
import { useRecoilValue } from "recoil";
import { environtmentState } from "../stores";

export default function ViewCurrentEnv() {
  const env = useRecoilValue(environtmentState);
  const selected: any = env.list.find((d: any) => d.id === env.selected);
  const codes = `{{foo}}`;
  return (
    <div
      style={{
        paddingLeft: 15,
        paddingRight: 15,
        width: "50vw",
        textAlign: "left",
      }}
    >
      <Text h5 size={14}>
        {selected?.name}
      </Text>
      <div style={{ overflow: "auto", maxHeight: "35vh" }}>
        <Table data={selected?.variables || []}>
          <Table.Column prop="name" label="key" />
          <Table.Column prop="value" label="value" />
        </Table>
      </div>
      <Text size={12}>
        Use variables to reuse values in different places. The value is
        auto-updated to reflect the current value. you can use it in URL request
        and headers request value
        <br />
        <br />
        how to use variable? <Code>{codes}</Code>
      </Text>
    </div>
  );
}
