import React from "react";
import { Tabs } from "@geist-ui/react";
import { useRecoilState } from "recoil";
import { uiState } from "../stores";
import MainRequestRestBody from "./MainRequestRestBody";
import MainRequestHeader from "./MainRequestHeader";

export default function RestRequest() {
  const [ui, setUi] = useRecoilState(uiState);
  return (
    <div style={{ height: "calc(30vh + 35px)" }}>
      <Tabs
        initialValue={ui.requestTab}
        onChange={(e) => setUi((p) => ({ ...p, requestTab: e }))}
      >
        <Tabs.Item label="Headers" value="Headers">
          <MainRequestHeader />
        </Tabs.Item>
        <Tabs.Item label="Body" value="Body">
          <MainRequestRestBody />
        </Tabs.Item>
      </Tabs>
    </div>
  );
}
