import React from "react";
import { GeistProvider, CssBaseline } from "@geist-ui/react";

import { useRecoilState } from "recoil";
import { appState } from "./stores";

import Header from "./components/Header";
import Sidebar from "./components/Sidebar";
import Footer from "./components/Footer";
import Main from "./components/Main";
import TitleBar from "./components/TitleBar";
import "./App.css";

const thisWindow: any = window;

const App: React.FC = () => {
  const [app, setApp] = useRecoilState(appState);
  React.useEffect(() => {
    thisWindow.contextElectron.appConfig().then((res: any) => {
      setApp((p) => ({ ...p, ...res }));
    });
  }, [setApp]);

  const scrollbarColor = app.darkMode
    ? "rgba(255, 255, 255, 0.1)"
    : "rgba(0, 0, 0, 0.1)";

  return (
    <GeistProvider theme={{ type: app.darkMode ? "dark" : "light" }}>
      <CssBaseline />
      <TitleBar />
      <Header />
      <div className="flex grow">
        <Sidebar />
        <Main />
      </div>
      <Footer />
      <style>{`
        * {
          scrollbar-width: thin;
          scrollbar-color: ${scrollbarColor} transparent;
        }

        *::-webkit-scrollbar {
          width: 12px;
          border-left: 1px solid ${scrollbarColor};
        }
        *::-webkit-scrollbar-track {
          background: transparent;
        }
        *::-webkit-scrollbar-thumb {
          background-color: ${scrollbarColor};
          border-radius: 0px;
        }
      `}</style>
    </GeistProvider>
  );
};

export default App;
