import React from "react";
import {
  Text,
  Input,
  Button,
  Popover,
  Modal,
  useModal,
  useInput,
  Spacer,
} from "@geist-ui/react";
import { createCollection, deleteCollection, deleteRequest } from "../db";
import { useRecoilValue, useResetRecoilState } from "recoil";
import { requestState, responseState } from "../stores";

const thisWindow: any = window;
thisWindow.getAllCollection = function () {};

export function ModalCreateCollection(props: any) {
  const input = useInput("");
  return (
    <Modal {...props.bindings}>
      <Modal.Title>Create new collection</Modal.Title>
      <div style={{ marginTop: 15 }}>
        <Input
          {...input.bindings}
          label="name"
          placeholder="please input collection name"
          width="100%"
        />
      </div>
      <Modal.Action passive onClick={() => props.setVisible(false)}>
        close
      </Modal.Action>
      <Modal.Action
        disabled={input.state.length < 2}
        onClick={() => {
          createCollection(input.state).then(() => {
            thisWindow.getAllCollection();
            props.setVisible(false);
          });
        }}
      >
        save
      </Modal.Action>
    </Modal>
  );
}

export default function CollectionMenu() {
  const modalAdd = useModal();
  const [visible, setVisible] = React.useState(false);
  const resetRequest = useResetRecoilState(requestState);
  const resetResponse = useResetRecoilState(responseState);
  const request = useRecoilValue(requestState);

  return (
    <>
      <div
        className="flex spaceBetween"
        style={{ textAlign: "center", width: "100%" }}
      >
        <Button size="mini" onClick={() => modalAdd.setVisible(true)}>
          New collection
        </Button>
        <Popover
          content={
            <div style={{ padding: "10px 20px" }}>
              <Text small type="secondary">
                Are you sure you want to clear all your collection?
              </Text>
              <Spacer y={0.5} />
              <Button
                size="mini"
                onClick={() => {
                  deleteCollection().then(() => {
                    deleteRequest().then(() => {
                      thisWindow.getAllCollection();
                      if (request.request_id) {
                        resetRequest();
                        resetResponse();
                      }
                      setVisible(false);
                    });
                  });
                }}
              >
                Yes
              </Button>
              <Spacer x={1} inline />
              <Button
                type="secondary-light"
                size="mini"
                onClick={() => setVisible(false)}
              >
                No
              </Button>
            </div>
          }
          visible={visible}
          onVisibleChange={setVisible}
        >
          <Button onClick={() => setVisible(true)} size="mini">
            clear all
          </Button>
        </Popover>
      </div>
      <ModalCreateCollection {...modalAdd} />
    </>
  );
}
