import React from "react";
import { useTheme, Text } from "@geist-ui/react";
import styles from "./ListItem.module.css";
import Tag from "./Tag";
import useSendRequest from "../hooks/useSendRequest";

const ListItem = React.memo((props: any) => {
  const { palette } = useTheme();
  const { setHistoryRequest } = useSendRequest();
  return (
    <div
      className={styles.list}
      style={{ borderBottom: `1px solid ${palette.accents_2}` }}
    >
      <button
        onClick={() => setHistoryRequest({ history_id: props.id, ...props })}
        style={{ width: "100%" }}
      >
        <Text small size={12}>
          {props.url}
        </Text>
      </button>
      <Tag method={props.method} />
      <Text style={{ marginLeft: 10 }} type="secondary" small size={11}>
        {new Date(props.updated_at).toLocaleString()}
      </Text>
    </div>
  );
});

export default ListItem;
