import React from "react";
import { Table, Input, Button, Toggle, useTheme, Text } from "@geist-ui/react";
import { Plus, Minus } from "@geist-ui/react-icons";
import Editor from "@monaco-editor/react";
import { transformArrayToObject, transformObjectToArray } from "../utils";

function InputList(props: any) {
  return (
    <Input
      size="mini"
      initialValue={props.value}
      onChange={(e) => props.onchange(e.target.value)}
      width="100%"
      placeholder={props.placeholder}
    />
  );
}

export default function DynamicTable(props: {
  onChange?: Function;
  data: any;
  height: string;
  useRaw?: boolean;
  hideChecked?: boolean;
  activeTab?: string;
  onChangeTab?: any;
}) {
  const editorHeadersRef: any = React.useRef(null);
  const { type, palette } = useTheme();
  const [state, setstate] = React.useState([
    { name: "", value: "", checked: true },
  ]);

  const isRaw = props.activeTab === "raw";

  function editorHeadersDidMount(_: any, editor: any) {
    editorHeadersRef.current = editor;
  }

  function onMouseLeave() {
    let newState = state;
    if (
      isRaw &&
      editorHeadersRef.current &&
      editorHeadersRef.current.getValue()
    ) {
      const newMutationArray = transformObjectToArray(
        JSON.parse(editorHeadersRef.current.getValue() || "{}")
      );
      newState = [
        ...state.filter((d) => !d.checked),
        ...newMutationArray.map((d) => ({ ...d, checked: true })),
      ];
    }
    props.onChange && props.onChange(newState);
  }

  React.useEffect(() => {
    setstate(props.data);
  }, [props.data]);

  function onchange(index: number, value: any, key: string) {
    let newState: any = [...state];
    newState[index] = {
      ...newState[index],
      [key]: value,
    };
    setstate([...newState]);
  }

  const data = [
    ...state.map((d, i) => ({
      checked: (
        <Toggle
          checked={d.checked}
          size="mini"
          onChange={(e) => onchange(i, e.target.checked, "checked")}
        />
      ),
      key: (
        <InputList
          value={d.name}
          placeholder="key"
          onchange={(val: any) => onchange(i, val, "name")}
        />
      ),
      value: (
        <InputList
          value={d.value}
          placeholder="value"
          onchange={(val: any) => onchange(i, val, "value")}
        />
      ),
      action: (
        <Button
          onClick={() => setstate(state.filter((_, ni) => ni !== i))}
          style={{ minWidth: 0 }}
          size="mini"
        >
          <Minus size={14} />
        </Button>
      ),
    })),
  ];

  function onAdd() {
    setstate((p) => [...p, { value: "", name: "", checked: true }]);
  }

  return (
    <>
      {props.useRaw && (
        <div
          className="flex"
          style={{
            padding: "8px 10px 8px 10px",
            borderBottom: `1px solid ${palette.accents_2}`,
          }}
        >
          <Toggle
            checked={isRaw}
            size="mini"
            onChange={(e) =>
              props.onChangeTab(e.target.checked ? "raw" : "form-data")
            }
          />
          <Text type="secondary" style={{ marginLeft: 10 }} small>
            Raw
          </Text>
        </div>
      )}

      <div
        style={{
          height: props.height,
          overflow: isRaw ? "hidden" : "auto",
        }}
        onMouseLeave={onMouseLeave}
      >
        {isRaw ? (
          <Editor
            height={props.height}
            editorDidMount={editorHeadersDidMount}
            theme={type}
            width="100%"
            value={JSON.stringify(transformArrayToObject(state), null, 2)}
            options={{
              minimap: { enabled: false },
              renderLineHighlight: false,
            }}
            language="json"
          />
        ) : (
          <Table
            data={[
              ...data,
              {
                checked: null,
                key: "",
                value: "Add new",
                action: (
                  <Button style={{ minWidth: 0 }} size="mini" onClick={onAdd}>
                    <Plus size={14} />
                  </Button>
                ),
              },
            ]}
            hover={false}
          >
            {!props.hideChecked && (
              <Table.Column prop="checked" label="" width={30} />
            )}
            <Table.Column prop="key" label="Key" width={200} />
            <Table.Column prop="value" label="Value" />
            <Table.Column prop="action" label="Action" width={100} />
          </Table>
        )}
      </div>
    </>
  );
}
