import React from "react";
import { useRecoilState } from "recoil";
import { requestState } from "../stores";
import {
  Select,
  Input,
  ButtonGroup,
  Button,
  useModal,
  ButtonDropdown,
  Breadcrumbs,
} from "@geist-ui/react";
import { METHODS } from "../constants";
import { Code, ExternalLink, Send } from "@geist-ui/react-icons";
import { ModalShare, ModalCode, ModalSaveRequest } from "./MainHeaderModal";
import useSendRequest from "../hooks/useSendRequest";
import useSaveRequest from "../hooks/useSaveRequest";
import useDbValue from "../hooks/useDbValue";

export default () => {
  const [request, setRequest] = useRecoilState(requestState);
  const ref: any = React.useRef(null);
  const modalShare = useModal();
  const modalCode = useModal();
  const modalSave = useModal();
  const { sendRequest } = useSendRequest();
  const { saveRequest } = useSaveRequest();
  const dbValue: any = useDbValue("collections", request.collection_id);

  function onChange(name: string, value: any) {
    setRequest((p: any) => ({
      ...p,
      [name]: value,
      history_id: null,
    }));
  }

  React.useEffect(() => {
    ref && (ref.current.value = request.url || "");
  }, [request.url]);

  return (
    <>
      <div className="flex spaceBetween">
        <div style={{ margin: 0, padding: "5px 10px 0px 10px" }}>
          <Breadcrumbs size="mini">
            {dbValue?.name && (
              <Breadcrumbs.Item>{dbValue.name}</Breadcrumbs.Item>
            )}
            <Breadcrumbs.Item>
              {request.name || "untitled request"}
            </Breadcrumbs.Item>
          </Breadcrumbs>
        </div>
      </div>
      <div
        className="flex"
        style={{
          padding: "0px 10px 0px 10px",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <Select
          placeholder="Choose one"
          value={request.method || "GET"}
          onChange={(e) => onChange("method", e)}
          size="small"
          style={{ marginRight: 10 }}
        >
          {METHODS.map((d: any) => (
            <Select.Option key={d} value={d}>
              {d}
            </Select.Option>
          ))}
        </Select>
        <div style={{ flexGrow: 1 }}>
          <Input
            onChange={(e) => onChange("url", e.target.value)}
            initialValue={request.url || ""}
            placeholder="Enter request URL"
            size="small"
            width="100%"
            ref={ref}
            onKeyDown={(e) => {
              if (e.key === "Enter") {
                sendRequest();
              }
            }}
          />
        </div>

        <ButtonGroup size="small" type="secondary-light">
          <Button
            onClick={sendRequest}
            loading={request.status === "LOADING"}
            disabled={!request.url}
          >
            <span style={{ marginRight: 10 }}>Send</span>
            <Send size={14} />
          </Button>
        </ButtonGroup>
        <ButtonDropdown size="small">
          <ButtonDropdown.Item onClick={() => saveRequest(modalSave)} main>
            Save
          </ButtonDropdown.Item>
          <ButtonDropdown.Item onClick={() => modalSave.setVisible(true)}>
            Save As..
          </ButtonDropdown.Item>
        </ButtonDropdown>
        <ButtonGroup size="small" disabled={!request.url}>
          <Button onClick={() => modalCode.setVisible(true)}>
            <Code size={16} />
          </Button>
          <Button onClick={() => modalShare.setVisible(true)}>
            <ExternalLink size={16} />
          </Button>
        </ButtonGroup>
      </div>
      {modalShare.visible && <ModalShare {...modalShare} />}
      {modalCode.visible && <ModalCode {...modalCode} />}
      {modalSave.visible && <ModalSaveRequest {...modalSave} />}
    </>
  );
};
