import React from "react";
import { RecoilRoot, useRecoilTransactionObserver_UNSTABLE } from "recoil";
import ReactDOM from "react-dom";
import App from "./App";
import stores from "./stores";
import db from "./db";
import * as serviceWorker from "./serviceWorker";

db.version(1).stores({
  history: "++id",
  collections: "++id",
  requests: "++id,collection_id",
});

const newStores: any = stores;

function DebugObserver() {
  useRecoilTransactionObserver_UNSTABLE(({ snapshot }) => {
    let state: any = {};
    Object.keys(newStores).forEach(function (key: string) {
      state[key] = snapshot.getLoadable(newStores[key]).contents;
    });
    window.localStorage.setItem("APP_STATE", JSON.stringify(state));
  });
  return null;
}

const initializeState = (props: any) => {
  if (window.localStorage.getItem("APP_STATE")) {
    const localState = JSON.parse(
      window.localStorage.getItem("APP_STATE") || "{}"
    );
    Object.keys(localState).forEach(function (key: string) {
      props.set(newStores[key], localState[key]);
    });
  }
};

ReactDOM.render(
  <RecoilRoot initializeState={initializeState}>
    <DebugObserver />
    <App />
  </RecoilRoot>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
