import { putHistory } from "../db";
import { fetch } from "../fetch";
import { useRecoilState, useRecoilValue, useResetRecoilState } from "recoil";
import {
  requestState,
  responseState,
  uiState,
  appState,
  DEFAULT_REQUEST_STATE,
  environtmentState,
} from "../stores";
import qs from "qs";
import { AxiosRequestConfig } from "axios";
import { transformArrayToObject } from "../utils";

export default function useSendRequest() {
  const [request, setRequest] = useRecoilState(requestState);
  const [, setResponse] = useRecoilState(responseState);
  const resetRequest = useResetRecoilState(requestState);
  const resetResponse = useResetRecoilState(responseState);
  const env = useRecoilValue(environtmentState);
  const [ui, setUi] = useRecoilState(uiState);
  const app = useRecoilValue(appState);

  const sendRequest = () => {
    setRequest((p) => ({ ...p, status: "LOADING" }));
    setUi((p) => ({ ...p, showDocs: false }));

    let requestConfig: AxiosRequestConfig = {
      url: request.url,
      method: request.method === "GRAPHQL" ? "POST" : request.method,
      headers: {
        "Content-Type": "application/json",
        ...transformArrayToObject(request.headers),
      },
    };

    if (app.noCacheHeader) requestConfig.headers["Cache-Control"] = "no-cache";

    if (!app.useSytemProxy) {
      requestConfig.proxy = {
        host: app.server,
        port: app.port,
        auth: {
          username: app.proxyUsername,
          password: app.proxyPassword,
        },
      };
    }

    if (!app.followRedirects) requestConfig.maxRedirects = 0;

    switch (ui.requestBodyTab) {
      case "x-www-form-urlencoded":
        requestConfig.headers["Content-Type"] =
          "application/x-www-form-urlencoded";
        requestConfig.data = qs.stringify(
          transformArrayToObject(request["x-www-form-urlencoded"])
        );
        break;

      case "none":
        requestConfig.data = undefined;
        break;

      case "binary":
        requestConfig.headers["Content-Type"] = "multipart/form-data";
        const formData = new FormData();
        const thisWindow: any = window;
        formData.append("file", thisWindow.tempFile);
        break;

      default:
        requestConfig.data = request.data;
        requestConfig.params = request.params;
        break;
    }

    if (request.method === "GRAPHQL") {
      requestConfig.data = request.data;
    }

    if (env.selected) {
      const selected: any = env.list.find((d: any) => d.id === env.selected);
      if (selected) {
        const stringHeaders = JSON.stringify(requestConfig.headers);
        selected.variables.forEach((d: any) => {
          requestConfig.url = requestConfig.url?.replace(
            `{{${d.name}}}`,
            d.value
          );
          stringHeaders.replace(`{{${d.name}}}`, d.value);
        });
        requestConfig.headers = JSON.parse(stringHeaders);
      }
    }

    const thisWindow: any = window;

    if (app.useHttpRequest) {
      thisWindow.contextElectron.nodeAxios(requestConfig).then((res: any) => {
        setResponse(res);
        const { history_id, request_id, ...rest } = request;
        putHistory({ id: request.history_id, ...rest }).then((resId: any) => {
          setRequest((p) => ({ ...p, history_id: resId, status: "" }));
          let thisWindow: any = window;
          thisWindow.getListHistory();
        });
      });
      return;
    }

    fetch(requestConfig)
      .then((res) => setResponse(res))
      .catch((err) => setResponse(err))
      .finally(() => {
        const { history_id, request_id, ...rest } = request;
        putHistory({ id: request.history_id, ...rest }).then((resId: any) => {
          setRequest((p) => ({ ...p, history_id: resId, status: "" }));
          let thisWindow: any = window;
          thisWindow.getListHistory();
        });
      });
  };

  function newRequest(collection_id?: number | undefined) {
    if (collection_id) {
      setRequest({ ...DEFAULT_REQUEST_STATE, collection_id });
    } else {
      resetRequest();
    }
    resetResponse();
  }

  function setHistoryRequest(data: any) {
    const { id, created_at, updated_at, ...rest } = data;
    setRequest({ ...DEFAULT_REQUEST_STATE, ...rest });
    resetResponse();
  }

  return {
    sendRequest,
    newRequest,
    setHistoryRequest,
  };
}
