import React from "react";
import { useTheme, Text } from "@geist-ui/react";
import { ChevronDown, ChevronRight } from "@geist-ui/react-icons";
import styles from "./ListItem.module.css";
import { useRecoilState, useResetRecoilState } from "recoil";
import { requestState, responseState, uiState } from "../stores";
import ListMore from "./ListMore";
import Tag from "./Tag";

const ListRequest = React.memo((props: any) => {
  const [, setRequest] = useRecoilState(requestState);
  const resetResponse = useResetRecoilState(responseState);

  function onChangeRequest() {
    resetResponse();
    const { created_at, updated_at, ...rest } = props.data;
    setRequest({
      request_id: props.id,
      collection_id: props.collectionId,
      name: props.name,
      status: "",
      ...rest,
    });
  }

  return (
    <div className="flex" style={{ padding: "7px 0px", alignItems: "center" }}>
      <Tag method={props.data.method} />
      <button onClick={onChangeRequest} style={{ marginLeft: 7, flexGrow: 1 }}>
        <Text size={12} type="secondary" span>
          {props.name}
        </Text>
      </button>
      <ListMore db="requests" id={props.id} />
    </div>
  );
});

const ListCollction = React.memo(
  (props: { id: number; name: string; requests: any[]; total: number }) => {
    const { palette } = useTheme();
    const [ui, setUi] = useRecoilState(uiState);

    function onClick() {
      if (ui.collectionTree.includes(props.id)) {
        setUi((p: any) => ({
          ...p,
          collectionTree: p.collectionTree.filter(
            (d: number) => d !== props.id
          ),
        }));
      } else {
        setUi((p: any) => ({
          ...p,
          collectionTree: [...p.collectionTree, props.id],
        }));
      }
    }

    const show = ui.collectionTree.includes(props.id);

    return (
      <div
        className={styles.list}
        style={{
          borderBottom: `1px solid ${palette.accents_2}`,
          lineHeight: 1,
          flexDirection: "column",
        }}
      >
        <div className="flex spaceBetween">
          <button
            className="flex"
            style={{ width: "100%", textAlign: "left", alignItems: "center" }}
            onClick={onClick}
          >
            {show ? <ChevronDown size={12} /> : <ChevronRight size={12} />}
            <div style={{ marginLeft: 10 }}>
              <div>
                <Text size={12} span>
                  {props.name}
                </Text>
              </div>
              <Text size={11} type="secondary" span>
                {props.total} requests
              </Text>
            </div>
          </button>
          <ListMore db="collections" id={props.id} />
        </div>
        {show && (props.requests || []).length > 0 && (
          <div
            style={{
              marginTop: 10,
              paddingTop: 5,
              paddingLeft: 30,
              marginLeft: -10,
              borderTop: `1px solid ${palette.accents_2}`,
            }}
          >
            {(props.requests || []).map((d: number, i) => (
              <ListRequest key={i} {...d} collectionId={props.id} />
            ))}
          </div>
        )}
      </div>
    );
  }
);

export default ListCollction;
