import React from "react";
import { useRecoilValue } from "recoil";
import { requestState } from "../stores";
import GraphQlRequest from "./MainRequestGraphql";
import RestRequest from "./MainRequestRest";

export default () => {
  const request = useRecoilValue(requestState);

  if (request.method === "GRAPHQL") {
    return <GraphQlRequest />;
  }

  return <RestRequest />;
};
