import React from "react";
import {
  useTheme,
  Tabs,
  Text,
  Button,
  Divider,
  Display,
} from "@geist-ui/react";
import { useRecoilState } from "recoil";
import { requestState, uiState } from "../stores";
import axios from "axios";
import { GRAPHQL_SCHEMA_QUERY } from "../constants";

function QueryName(props: any) {
  let args = "";
  props.args.map((a: any, i: number) => {
    args += ", " + a.name;
    if (a.type.ofType?.name) {
      args = args + ": " + a.type.ofType.name;
    }
    if (!a.type.ofType) {
      args = args + ": " + a.type.name;
    }
    if (a.type.ofType?.kind === "LIST") {
      args = args + ": [" + a.type.ofType.ofType.ofType.name + "]";
    }
  });
  args = args.slice(2);
  return (
    <div>
      <Text small style={{ margin: 0 }}>
        {props.name}(<strong>{args}</strong>):{" "}
        <strong>
          {props.type?.kind === "LIST"
            ? `[${props.type.ofType.name}]`
            : props.type.name}
        </strong>
      </Text>
    </div>
  );
}

function TypeName(props: any) {
  return (
    <div>
      <Text style={{ paddingLeft: 15 }} h5>
        {props.name}
      </Text>
      <div style={{ paddingLeft: 25, marginBottom: 15 }}>
        {props.fields.map((d: any, i: number) => (
          <div key={i}>
            <div>
              <Text small>
                {d.name}:{" "}
                <strong>
                  {" "}
                  {d.type.kind === "LIST"
                    ? `[${d.type.ofType.name}]`
                    : d.type.name}
                </strong>
              </Text>
            </div>
            <Text type="secondary" small size={12}>
              {d.description}
            </Text>
            <Divider y={0.5} />
          </div>
        ))}
      </div>
    </div>
  );
}

function Docs(props: { data: any }) {
  const listQuery = props.data.types.find(
    (d: any) => d.name.toLowerCase() === "query"
  );
  const listTypes = props.data.types.filter(
    (d: any) =>
      d.kind === "OBJECT" && !d.name.includes("__") && d.name !== "Query"
  );
  return (
    <Tabs initialValue="2">
      <Tabs.Item label="Queries" value="1">
        <div
          style={{
            padding: 10,
            overflow: "auto",
            height: "calc(100vh - 230px)",
          }}
        >
          {(listQuery.fields || []).map((d: any) => {
            return (
              <div key={d.name}>
                {/* <Description title={d.name} content={d.description} /> */}
                <QueryName {...d} />
                <Text type="secondary" small size={12}>
                  {d.description}
                </Text>
                <Divider y={1} />
              </div>
            );
          })}
        </div>
      </Tabs.Item>
      <Tabs.Item label="Types" value="2">
        <div
          style={{
            paddingTop: 15,
            overflow: "auto",
            height: "calc(100vh - 230px)",
          }}
        >
          {(listTypes || []).map((d: any) => {
            return (
              <div key={d.name}>
                <TypeName {...d} />
              </div>
            );
          })}
        </div>
      </Tabs.Item>
    </Tabs>
  );
}

export default function DocsGraphQl() {
  const [loading, setLoading] = React.useState(false);
  const [request, setRequest] = useRecoilState(requestState);
  const [ui, setUi] = useRecoilState(uiState);
  const { palette } = useTheme();

  function getSchema() {
    setLoading(true);
    axios
      .post(request.url || "", {
        query: GRAPHQL_SCHEMA_QUERY,
      })
      .then((res) => {
        const schema = res.data?.__schema || res.data.data?.__schema;
        if (schema) {
          setRequest((p) => ({ ...p, schema }));
        }
      })
      .finally(() => {
        setLoading(false);
      });
  }

  return (
    <div style={{ position: "absolute", right: 15 }}>
      <Button
        onClick={() => setUi((p) => ({ ...p, showDocs: !p.showDocs }))}
        size="mini"
      >
        {ui.showDocs ? "Hide Docs" : "Docs"}
      </Button>
      {ui.showDocs && (
        <div
          style={{
            position: "absolute",
            top: 31,
            right: -15,
            width: "calc(40vw)",
            height: "calc(100vh - 183px)",
            zIndex: 3,
            background: palette.background,
            border: `1px solid ${palette.accents_2}`,
          }}
        >
          {!request.schema ? (
            <div>
              <Display caption="no graphql schema available">
                <Button loading={loading} onClick={getSchema} size="small">
                  try get schema
                </Button>
              </Display>
            </div>
          ) : (
            <Docs data={request.schema} />
          )}
        </div>
      )}
    </div>
  );
}
